@keyframes dropdown {
  0% {
    transform: translateX(-80%);
  }
  100% {
    transform: translateY(0);
  }
}

button {
  cursor: pointer;
}

.navbar {
  display: flex;
  margin-top: auto;
  margin-bottom: 1rem;
  margin-left: 2rem;
  width: 20rem;
}

.navbar__wrapper {
  display: flex;
  position: relative;
}

.navbar__depth1 {
  z-index: 999;
}

.navbar__depth2 {
  animation: dropdown 0.4s ease;
}

.menu {
  background-color: var(--navbar-background-color);
  border: 1px solid var(--navbar-background-color);
  display: flex;
  align-items: center;
  color: var(--text-basic-color);
  padding: 1rem 4rem 1rem 1rem;
  border-radius: 5px;
  margin: 1rem 0.3rem;
  width: 4rem;
  font-size: large;
  font-weight: bold;
}

.menu:hover {
  background-color: var(--main-background-color);
  color: var(--navbar-background-color);
}

.menu__depth2 {
  background-color: var(--navbar-depth2-color);
  border: 1px solid var(--navbar-depth2-color);
  display: flex;
  align-items: center;
  color: var(--text-basic-color);
  padding: 1rem 2rem 1rem 1rem;
  border-radius: 5px;
  margin: 1rem 0.3rem;
  width: 8rem;
  font-size: large;
  font-weight: bold;
}

.menu__depth2:hover {
  background-color: var(--main-background-color);
  color: var(--navbar-depth2-color);
}
